<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="" prop="biaoti" >
					<el-input v-model="ruleForm.biaoti" placeholder="" clearable  :readonly="ro.biaoti"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="biaoti" >
					<el-input v-model="ruleForm.biaoti" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="yonghuzhanghao" >
					<el-input v-model="ruleForm.yonghuzhanghao" placeholder="" clearable  :readonly="ro.yonghuzhanghao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yonghuzhanghao" >
					<el-input v-model="ruleForm.yonghuzhanghao" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="yonghuxingming" >
					<el-input v-model="ruleForm.yonghuxingming" placeholder="" clearable  :readonly="ro.yonghuxingming"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yonghuxingming" >
					<el-input v-model="ruleForm.yonghuxingming" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.touxiang" label="" prop="touxiang" >
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
						@change="touxiangUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.touxiang" label="" prop="touxiang" >
					<img v-if="ruleForm.touxiang.substring(0,4)=='http'&&ruleForm.touxiang.split(',w').length>1" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.touxiang" width="100" height="100">
					<img v-else-if="ruleForm.touxiang.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.touxiang.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.touxiang.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="/kg" prop="tizhong" >
					<el-input-number v-model="ruleForm.tizhong" placeholder="/kg" :disabled="ro.tizhong"></el-input-number>
				</el-form-item>
				<el-form-item v-else class="input" label="/kg" prop="tizhong" >
					<el-input v-model="ruleForm.tizhong" placeholder="/kg" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="xueya" >
					<el-input-number v-model="ruleForm.xueya" placeholder="" :disabled="ro.xueya"></el-input-number>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="xueya" >
					<el-input v-model="ruleForm.xueya" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="xuetang" >
					<el-input-number v-model="ruleForm.xuetang" placeholder="" :disabled="ro.xuetang"></el-input-number>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="xuetang" >
					<el-input v-model="ruleForm.xuetang" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="xinlv" >
					<el-input-number v-model="ruleForm.xinlv" placeholder="" :disabled="ro.xinlv"></el-input-number>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="xinlv" >
					<el-input v-model="ruleForm.xinlv" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="tiwen" >
					<el-input-number v-model="ruleForm.tiwen" placeholder="" :disabled="ro.tiwen"></el-input-number>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="tiwen" >
					<el-input v-model="ruleForm.tiwen" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="huxipinlv" >
					<el-input v-model="ruleForm.huxipinlv" placeholder="" clearable  :readonly="ro.huxipinlv"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="huxipinlv" >
					<el-input v-model="ruleForm.huxipinlv" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="yundongbushu" >
					<el-input v-model="ruleForm.yundongbushu" placeholder="" clearable  :readonly="ro.yundongbushu"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yundongbushu" >
					<el-input v-model="ruleForm.yundongbushu" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="" prop="dengjishijian" >
					<el-date-picker
						format="yyyy  MM  dd "
						value-format="yyyy-MM-dd"
						v-model="ruleForm.dengjishijian" 
						type="date"
						:readonly="ro.dengjishijian"
						placeholder=""
					></el-date-picker> 
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.dengjishijian" label="" prop="dengjishijian" >
					<el-input v-model="ruleForm.dengjishijian" placeholder="" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item v-if="type!='info'"  label="" prop="jiankangqingkuang" >
				<editor 
					style="min-width: 200px; max-width: 600px;"
					v-model="ruleForm.jiankangqingkuang" 
					class="editor"
					myQuillEditor="jiankangqingkuang"
					action="file/upload">
				</editor>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.jiankangqingkuang" label="" prop="jiankangqingkuang" >
				<span class="text ql-snow ql-editor" v-html="ruleForm.jiankangqingkuang"></span>
			</el-form-item>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	import { 
		isNumber,
	} from "@/utils/validate";
	export default {
		data() {
			var validateNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isNumber(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					biaoti : false,
					yonghuzhanghao : false,
					yonghuxingming : false,
					touxiang : false,
					tizhong : false,
					xueya : false,
					xuetang : false,
					xinlv : false,
					tiwen : false,
					huxipinlv : false,
					yundongbushu : false,
					dengjishijian : false,
					jiankangqingkuang : false,
				},
			
				ruleForm: {
					biaoti: '',
					yonghuzhanghao: '',
					yonghuxingming: '',
					touxiang: '',
					tizhong: '',
					xueya: '',
					xuetang: '',
					xinlv: '',
					tiwen: '',
					huxipinlv: '',
					yundongbushu: '',
					dengjishijian: '',
					jiankangqingkuang: '',
				},

				rules: {
					biaoti: [
					],
					yonghuzhanghao: [
					],
					yonghuxingming: [
					],
					touxiang: [
					],
					tizhong: [
						{ validator: validateNumber, trigger: 'blur' },
					],
					xueya: [
						{ validator: validateNumber, trigger: 'blur' },
					],
					xuetang: [
						{ validator: validateNumber, trigger: 'blur' },
					],
					xinlv: [
						{ validator: validateNumber, trigger: 'blur' },
					],
					tiwen: [
						{ validator: validateNumber, trigger: 'blur' },
					],
					huxipinlv: [
					],
					yundongbushu: [
					],
					dengjishijian: [
					],
					jiankangqingkuang: [
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
			this.ruleForm.dengjishijian = this.getCurDate()
		},
		methods: {
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='biaoti'){
							this.ruleForm.biaoti = obj[o];
							this.ro.biaoti = true;
							continue;
						}
						if(o=='yonghuzhanghao'){
							this.ruleForm.yonghuzhanghao = obj[o];
							this.ro.yonghuzhanghao = true;
							continue;
						}
						if(o=='yonghuxingming'){
							this.ruleForm.yonghuxingming = obj[o];
							this.ro.yonghuxingming = true;
							continue;
						}
						if(o=='touxiang'){
							this.ruleForm.touxiang = obj[o];
							this.ro.touxiang = true;
							continue;
						}
						if(o=='tizhong'){
							this.ruleForm.tizhong = obj[o];
							this.ro.tizhong = true;
							continue;
						}
						if(o=='xueya'){
							this.ruleForm.xueya = obj[o];
							this.ro.xueya = true;
							continue;
						}
						if(o=='xuetang'){
							this.ruleForm.xuetang = obj[o];
							this.ro.xuetang = true;
							continue;
						}
						if(o=='xinlv'){
							this.ruleForm.xinlv = obj[o];
							this.ro.xinlv = true;
							continue;
						}
						if(o=='tiwen'){
							this.ruleForm.tiwen = obj[o];
							this.ro.tiwen = true;
							continue;
						}
						if(o=='huxipinlv'){
							this.ruleForm.huxipinlv = obj[o];
							this.ro.huxipinlv = true;
							continue;
						}
						if(o=='yundongbushu'){
							this.ruleForm.yundongbushu = obj[o];
							this.ro.yundongbushu = true;
							continue;
						}
						if(o=='dengjishijian'){
							this.ruleForm.dengjishijian = obj[o];
							this.ro.dengjishijian = true;
							continue;
						}
						if(o=='jiankangqingkuang'){
							this.ruleForm.jiankangqingkuang = obj[o];
							this.ro.jiankangqingkuang = true;
							continue;
						}
					}
				}
				// 
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
						if(((json.yonghuzhanghao!=''&&json.yonghuzhanghao) || json.yonghuzhanghao==0) && this.$storage.get("role")!=""){
							this.ruleForm.yonghuzhanghao = json.yonghuzhanghao
							this.ro.yonghuzhanghao = true;
						}
						if(((json.yonghuxingming!=''&&json.yonghuxingming) || json.yonghuxingming==0) && this.$storage.get("role")!=""){
							this.ruleForm.yonghuxingming = json.yonghuxingming
							this.ro.yonghuxingming = true;
						}
						if(((json.touxiang!=''&&json.touxiang) || json.touxiang==0) && this.$storage.get("role")!=""){
							this.ruleForm.touxiang = json.touxiang
							this.ro.touxiang = true;
						}
					} else {
						this.$message.error(data.msg);
					}
				});
			
			},
			// 

			info(id) {
				this.$http({
					url: `jiankangshuju/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//
						let reg=new RegExp('../../../upload','g')//g
						this.ruleForm.jiankangqingkuang = this.ruleForm.jiankangqingkuang.replace(reg,'../../../djangov5gemqq6/upload');
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 
			async onSubmit() {
					if(this.ruleForm.touxiang!=null) {
						this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					if(!this.ruleForm.id) {
						delete this.ruleForm.userid
					}
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `jiankangshuju/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.jiankangshujuCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.jiankangshujuCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			touxiangUploadChange(fileUrls) {
				this.ruleForm.touxiang = fileUrls;
			},
		}
	};
</script>
